//CurrencyConverter.js
import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { db, collection, addDoc, deleteDoc, doc, onSnapshot, getDocs } from '../firebase'; //  onSnapshot  getDocs  Real-Time
import '../style/currencyConverter.css'; //  CSS  Currency Converter

function CurrencyConverter() {
  //  state 
  const [amount, setAmount] = useState(1);  // 
  const [convertedAmount, setConvertedAmount] = useState(0); // 
  const [exchangeRates, setExchangeRates] = useState({}); // 
  const [fromCurrency, setFromCurrency] = useState('USD');  // 
  const [toCurrency, setToCurrency] = useState('THB');  // 
  const [loading, setLoading] = useState(true);  // 
  const [exchangeHistory, setExchangeHistory] = useState([]);  // 

  //  API 
  useEffect(() => {
    const fetchExchangeRate = async () => {
      try {
        const response = await axios.get(`https://v6.exchangerate-api.com/v6/a20d39beb622510971171fb7/latest/${fromCurrency}`);
        setExchangeRates(response.data.conversion_rates); // 
        setLoading(false);  // 
      } catch (error) {
        console.error('Error fetching exchange rates:', error);
        setLoading(false);  // 
      }
    };

    fetchExchangeRate(); // 
  }, [fromCurrency]); // 

  // 
  useEffect(() => {
    if (exchangeRates[toCurrency]) {
      setConvertedAmount((amount * exchangeRates[toCurrency]).toFixed(2)); // 
    }
  }, [amount, exchangeRates, toCurrency]); // 

  // Real-Time Listener  Firestore
  useEffect(() => {
    const unsubscribe = onSnapshot(collection(db, 'currencyExchangeRates'), (querySnapshot) => {
      const history = querySnapshot.docs.map(doc => ({
        id: doc.id,
        ...doc.data(),
        timestamp: doc.data().timestamp.toDate(),
      }));
      setExchangeHistory(history); // 
    });

    return () => unsubscribe(); //  component 
  }, []); //  component 

  //  Firebase
  const saveDataToFirebase = async () => {
    try {
      await addDoc(collection(db, 'currencyExchangeRates'), {
        amount,
        fromCurrency,
        toCurrency,
        convertedAmount,
        timestamp: new Date(),
      });
      alert('Data saved successfully!');  // 
    } catch (e) {
      console.error('Error adding document: ', e);  // 
    }
  };

  //  Firebase
  const deleteDataFromFirebase = async (id) => {
    try {
      const docRef = doc(db, 'currencyExchangeRates', id);
      await deleteDoc(docRef);  //  Firestore
    } catch (e) {
      console.error('Error deleting document: ', e);  // 
    }
  };

  // 
  useEffect(() => {
    const clearDataOnUnload = () => {
      getDocs(collection(db, 'currencyExchangeRates')).then((querySnapshot) => {
        querySnapshot.forEach((doc) => {
          deleteDoc(doc.ref); // 
        });
      });
    };

    window.onbeforeunload = () => {
      clearDataOnUnload(); // 
    };

    return () => {
      window.onbeforeunload = null; //  component  unmount
    };
  }, []); //  component 

  return (
    <div className="currency-converter">
      <h1>Currency Converter</h1>

      {/*  */}
      <div>
        <label htmlFor="fromCurrency">From Currency: </label>
        <select
          id="fromCurrency"
          value={fromCurrency}
          onChange={(e) => setFromCurrency(e.target.value)} // 
        >
          <option value="USD">USD</option>
          <option value="EUR">EUR</option>
          <option value="GBP">GBP</option>
          <option value="JPY">JPY</option>
          <option value="AUD">AUD</option>
          <option value="THB">THB</option>
        </select>
      </div>

      {/*  */}
      <div>
        <input
          type="number"
          value={amount}
          onChange={(e) => setAmount(e.target.value)}  // 
          placeholder={`Enter amount in ${fromCurrency}`}
        />
      </div>

      {/*  */}
      <div>
        <label htmlFor="toCurrency">To Currency: </label>
        <select
          id="toCurrency"
          value={toCurrency}
          onChange={(e) => setToCurrency(e.target.value)} // 
        >
          <option value="USD">USD</option>
          <option value="EUR">EUR</option>
          <option value="GBP">GBP</option>
          <option value="JPY">JPY</option>
          <option value="AUD">AUD</option>
          <option value="THB">THB</option>
        </select>
      </div>

      {/*  */}
      {loading ? (
        <p>Loading exchange rates...</p>  // 
      ) : (
        <div>
          <p>
            {amount} {fromCurrency} = {convertedAmount} {toCurrency} {/*  */}
          </p>
        </div>
      )}

      <button onClick={saveDataToFirebase}>Save</button> {/*  */}

      <h2>Exchange History</h2>
      {/*  */}
      <ul>
        {exchangeHistory.map((record) => (
          <li key={record.id}>
            {record.amount} {record.fromCurrency} = {record.convertedAmount} {record.toCurrency} - 
            {record.timestamp instanceof Date && !isNaN(record.timestamp) ? 
            record.timestamp.toLocaleString() : 'Invalid Date'}
            <button onClick={() => deleteDataFromFirebase(record.id)}>Delete</button>  {/*  */}
          </li>
        ))}
      </ul>
    </div>
  );
}

export default CurrencyConverter;
